import React from 'react';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import {makeStyles} from "@mui/styles";

const columns = [
    { id: 'vacantion', label: 'Вакансии', minWidth: 170 },
    { id: 'newe', label: 'Новые отклики', minWidth: 100 },
    {
        id: 'waitCall',
        label: 'Ожидают звонка',
        minWidth: 170,
        align: 'center',
    },
    {
        id: 'pass',
        label: 'Прошли тестирование',
        minWidth: 170,
        align: 'center',
    },
    {
        id: 'waitPass',
        label: 'Ожидают собеседование',
        minWidth: 170,
        align: 'center',
    },
];

function createData(vacantion, newe,waitCall, pass, waitPass) {
    return { vacantion, newe, waitCall,pass, waitPass };
}

const rows = [
    createData('Юрист', 5,1,3,3 ),
    createData('Дизайнер', 5,1,3,3 ),
    createData('Бухгалтер', 5,1,3,3 ),
    createData('Бухгалтер', 5,1,3,3 ),
    createData('Front-End', 5,1,3,3 ),
    createData('QA-специалист', 5,1,3,3 ),
    createData('HR', 5,1,3,3 ),
    createData('Маркетолог', 5,1,3,3 ),

];
const useStyles = makeStyles({
    tabbleHead:{
        width:'100%',
        display:'flex',
        justifyContent:'space-between'
    },
    showAll: {
        fontFamily:'Montserrat',
        fontStyle:"normal",
        fontWeight:600,
        fontSize:'12px',
        color:'#3843ED',
        textDecorationLine:'underline',
        cursor:'pointer'
    },
    actualData:{
        color:'#20272E',
        fontSize:'16px',
        fontWeight:'500',
        lineHeight:'24px',
    }

});

const TableComponent = () => {

    const  classes = useStyles();

    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    return (<div>

            <Paper sx={{ width: '100%' }}>
                <TableContainer sx={{ maxHeight: 440 }}>
                    <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                <TableCell  align="center" colSpan={8}>
                                    <div className={classes.tabbleHead}>
                                        <div  className={classes.actualData}>
                                            Актуальные данные
                                        </div>
                                        <div className={classes.showAll} >
                                            Показать все
                                        </div>
                                    </div>
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                {columns.map((column) => (
                                    <TableCell
                                        key={column.id}
                                        align={column.align}
                                        style={{ top: 57, minWidth: column.minWidth }}
                                    >
                                        {column.label}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows
                                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                .map((row,index) => {
                                    return (
                                        <TableRow style={{backgroundColor:index % 2 === 0 ? '#f2f6ff':'white'}} hover role="checkbox" tabIndex={-1} key={row.code}>
                                            {columns.map((column) => {
                                                const value = row[column.id];
                                                return (
                                                    <TableCell key={column.id} align={column.align}>
                                                        {column.format && typeof value === 'number'
                                                            ? column.format(value)
                                                            : value}
                                                    </TableCell>
                                                );
                                            })}
                                        </TableRow>
                                    );
                                })}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={rows.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                />
            </Paper>



        </div>
    );
};

export default TableComponent;