import React from 'react';
import classes from './cards.module.css'
import Card from "./card/card";

const Cards = () => {
    return (
        <div className={classes.container}>
            <Card day={'04'} month={'may'} mark={true} remind={'Позвонить кандидату Шевченко...'} candidat={'Кандидат на должность Front ...'}/>
            <Card day={'04'} month={'may'} mark={false} remind={'Собеседование Коломиец'} candidat={'Кандидат на должность QA Manual'}/>
            <Card day={'04'} month={'may'} mark={false} remind={'Собеседование Швец'} candidat={'Кандидат на должность CTO'}/>
        </div>
    );
};

export default Cards;