import React from 'react';
import InputAdornment from '@mui/material/InputAdornment';
import TextField from '@mui/material/TextField';
import {makeStyles} from "@mui/styles";
import SearchPhoto from './Search.svg'


const useStyles = makeStyles({
    root: {
        backgroundColor:'#FFFFFF',
        boxShadow:'0px 3px 3px rgba(0, 0, 0, 0.08);',
        borderRadius:'20px',
        margin:'24px',
        width:'336px',
        height:'40px',
        // padding:'110px',
        justifyContent:'center'
    },
    insideStyle:{
        border:'transparent',
        marginLeft:'20px'
    },

});

const Search = () => {
    const MUAclasses = useStyles();

    return (
        <div style={{margin:'24px'}}>
                <TextField
                    id="input-with-icon-textfield"
                    placeholder='Найти на странице'
                    className={MUAclasses.root}
                    InputProps={{
                        disableUnderline: true,
                        startAdornment: (
                            <InputAdornment className={MUAclasses.insideStyle} position="start" >
                                <img src={SearchPhoto} alt=""/>
                            </InputAdornment>
                        ),
                    }}
                    variant="standard"
                />

        </div>
    );
};

export default Search;