import React from 'react';
import classes from './Right.module.css'
import icon1 from './Iconly/Bold/Message.svg'
import icon2 from './Iconly/Bold/Notification.svg'
import icon3 from './Group-11.png'

const RightNav = () => {
    return (
        <div className={classes.container}>
            <div className={classes.bottoms}>
                <img src={icon1} alt=""/>
                <img src={icon2} alt=""/>
            </div>
            <div className={classes.logo}>
                <img src={icon3} alt=""/>

            </div>

        </div>
    );
};

export default RightNav;