import React from 'react';
import classes from "./sideBar.module.css";
import HeaderBar from "./headerBar/headerBar";
import Calendar from '../../utils/images/sideBarIcons/Calendar.svg'
import user2 from '../../utils/images/sideBarIcons/2 User.png'
import user3 from '../../utils/images/sideBarIcons/3 User.svg'
import Document from '../../utils/images/sideBarIcons/Document.svg'
import Filter from '../../utils/images/sideBarIcons/Filter.svg'
import InfoSQ from '../../utils/images/sideBarIcons/Info Square.svg'
import Profile from '../../utils/images/sideBarIcons/Profile.svg'
import Setting from '../../utils/images/sideBarIcons/Setting.svg'
import Tick from '../../utils/images/sideBarIcons/Tick Square.svg'
import Work from '../../utils/images/sideBarIcons/Work.svg'
import Home from '../../utils/images/sideBarIcons/Home.svg'
import Heart from '../../utils/images/sideBarIcons/Heart.svg'
import Graph from '../../utils/images/sideBarIcons/Graph.svg'

import arrow from './Arrow - Left 3.png'

const SideBar = () => {
    return (
        <nav className={classes.copmonent}>
            <HeaderBar/>
            <div className={classes.itemContainer}>
                <div>
                    <img src={Home} alt=""/> <span>Главная</span>
                </div>
                <div>
                    <img src={Calendar} alt=""/> <span>Календарь</span>
                </div>
                <div>
                    <img src={Work} alt=""/> <span>Моя компания</span>
                </div>
                <div>
                    <img src={user3} alt=""/> <span>Отбор персонала</span>
                </div>
                <div>
                    <img src={Heart} alt=""/> <span>Адаптация персонала</span>
                </div>
                <div>
                    <img src={user2} alt=""/> <span>Оценка персонала</span>
                </div>
                <div>
                    <img src={Profile} alt=""/> <span>Оценка исполнителя</span>
                </div>
                <div>
                    <img src={Filter} alt=""/> <span>HR-администрирование</span>
                </div>
                <div>
                    <img src={InfoSQ} alt=""/> <span>Обучение персонала</span>
                </div>
                <div>
                    <img src={Document} alt=""/> <span>HR-аналитика</span>
                </div>
                <div>
                    <img src={Tick} alt=""/> <span>Опросы</span>
                </div>
                <div>
                    <img src={Graph} alt=""/> <span>Мои задачи</span>
                </div>
                <div>
                    <img src={Setting} alt=""/> <span>Настройки</span>
                </div>
                <img className={classes.arrow} src={arrow} alt=""/>
            </div>
        </nav>
    );
};

export default SideBar;