import React from 'react';
import classes from './greating.module.css'
import gitlImg from './Illistration.png'

const Greating = () => {
    return (
        <div className={classes.container}>
            <img className={classes.image} src={gitlImg} alt=""/>

            <div >
                <span className={classes.hi}>Привет, Юлия!</span><br/>
                <span className={classes.hiText}>Добро пожаловать в Ваш профиль, Вы получили новые отклики на Вакансии,
                рекомендуем ответить на них как можно быстрее, чтобы не упустить ценного сотрудника
            </span>

                <div>
                    <img src="" alt=""/>
                    <span className={classes.closeText}>Закрыть</span>
                </div>
            </div>
        </div>
    );
};

export default Greating;