import React from 'react';
import classes from './card.module.css'
import image from './Frame 1.png'

const Card = ({day, month, remind, candidat, mark}) => {
    return (
        <div className={classes.container}>
            <div className={classes.date}>
                <div>
                    {day}
                </div>
                <div>
                    {month}
                </div>
            </div>
            <div className={classes.text}>
                <div>
                    {remind}
                </div>
                <div className={classes.candidat}>
                    {candidat}
                </div>
            </div>
            {mark && <div className={classes.mark}>
                <img src={image} alt=""/>
            </div>}
        </div>
    );
};

export default Card;