import React from 'react';
import Card from "./card/card";
import classes from  './cards.module.css'

const Cards = () => {
    return (
        <div className={classes.cardsContainer}>
            <Card color={'#b6ffbd'} text={'Добавить кандидата'} />
            <Card color={'#ffeebf'} text={'Добавить вакансию'} />
            <Card color={'#dbdaff'} text={'Добавить встречу'} />
            <Card color={'#ffe4e2'} text={'Добавить задание'} />
        </div>
    );
};

export default Cards;